<template>
  <div class="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
    <div class="sm:mx-auto sm:w-full sm:max-w-md">
      <h2 class="mt-6 text-center text-3xl font-bold text-gray-900">
        –í–æ–π—Ç–∏ –≤ –∞–∫–∫–∞—É–Ω—Ç
      </h2>
      <p class="mt-2 text-center text-sm text-gray-600">
        –ò–ª–∏
        <router-link 
          to="/register" 
          class="font-medium text-blue-600 hover:text-blue-500"
        >
          —Å–æ–∑–¥–∞–π—Ç–µ –Ω–æ–≤—ã–π –∞–∫–∫–∞—É–Ω—Ç
        </router-link>
      </p>
    </div>

    <div class="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
      <div class="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
        <!-- Social Login Buttons -->
        <div class="space-y-3 mb-6">
          <button
            type="button"
            class="w-full flex justify-center items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            <img src="https://img.icons8.com/color/20/google-logo.png" class="mr-2" />
            –í–æ–π—Ç–∏ —á–µ—Ä–µ–∑ Google
          </button>
          
          <button
            type="button"
            class="w-full flex justify-center items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            <img src="https://img.icons8.com/ios-glyphs/20/github.png" class="mr-2" />
            –í–æ–π—Ç–∏ —á–µ—Ä–µ–∑ GitHub
          </button>
        </div>

        <div class="relative mb-6">
          <div class="absolute inset-0 flex items-center">
            <div class="w-full border-t border-gray-300" />
          </div>
          <div class="relative flex justify-center text-sm">
            <span class="px-2 bg-white text-gray-500">–ò–ª–∏ –≤–æ–π—Ç–∏ —Å email</span>
          </div>
        </div>

        <!-- Login Form -->
        <form class="space-y-6" @submit.prevent="handleLogin">
          <!-- Error message -->
          <div v-if="authStore.error" class="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded">
            {{ authStore.error }}
          </div>

          <div>
            <label for="email" class="block text-sm font-medium text-gray-700">
              Email –∞–¥—Ä–µ—Å
            </label>
            <div class="mt-1">
              <input
                id="email"
                name="email"
                type="email"
                autocomplete="email"
                required
                v-model="form.email"
                class="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                placeholder="your.email@example.com"
                :disabled="authStore.isLoading"
              />
            </div>
          </div>

          <div>
            <label for="password" class="block text-sm font-medium text-gray-700">
              –ü–∞—Ä–æ–ª—å
            </label>
            <div class="mt-1 relative">
              <input
                id="password"
                name="password"
                :type="showPassword ? 'text' : 'password'"
                autocomplete="current-password"
                required
                v-model="form.password"
                class="appearance-none block w-full px-3 py-2 pr-10 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
                :disabled="authStore.isLoading"
              />
              <button
                type="button"
                class="absolute inset-y-0 right-0 pr-3 flex items-center"
                @click="showPassword = !showPassword"
              >
                <span class="text-gray-400 hover:text-gray-600">
                  {{ showPassword ? 'üôà' : 'üëÅÔ∏è' }}
                </span>
              </button>
            </div>
          </div>

          <div class="flex items-center justify-between">
            <div class="flex items-center">
              <input
                id="remember-me"
                name="remember-me"
                type="checkbox"
                v-model="form.rememberMe"
                class="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
              />
              <label for="remember-me" class="ml-2 block text-sm text-gray-900">
                –ó–∞–ø–æ–º–Ω–∏—Ç—å –º–µ–Ω—è
              </label>
            </div>

            <div class="text-sm">
              <router-link 
                to="/forgot-password" 
                class="font-medium text-blue-600 hover:text-blue-500"
              >
                –ó–∞–±—ã–ª–∏ –ø–∞—Ä–æ–ª—å?
              </router-link>
            </div>
          </div>

          <div>
            <button
              type="submit"
              :disabled="authStore.isLoading || !isFormValid"
              class="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              <span v-if="authStore.isLoading" class="mr-2">‚è≥</span>
              {{ authStore.isLoading ? '–í—Ö–æ–¥...' : '–í–æ–π—Ç–∏' }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { useAuthStore } from '@/stores/auth';

const router = useRouter();
const route = useRoute();
const authStore = useAuthStore();

const form = ref({
  email: '',
  password: '',
  rememberMe: false,
});

const showPassword = ref(false);

const isFormValid = computed(() => {
  return form.value.email && form.value.password && form.value.email.includes('@');
});

const handleLogin = async () => {
  if (!isFormValid.value) return;

  try {
    await authStore.login({
      email: form.value.email,
      password: form.value.password,
    });

    // Redirect to intended page or dashboard
    const redirectTo = (route.query.redirect as string) || '/profile';
    router.push(redirectTo);
  } catch (error) {
    // Error is handled by the store
    console.error('Login failed:', error);
  }
};

// Clear any existing errors when component mounts
authStore.clearError();
</script>
